import React from "react";
import './Card.css';



export default function Card(props) {
   const {name,status,species,gender,image,onClose,origin,id} = props;
   return (
      <div className="cardContainer" >
         
         <button className="botonX" onClick={onClose}>X</button>
         
         <h2> {name} </h2>
         <h2>Status: {status}</h2>
         <h2>Species: {species}</h2>
         <h2>Gender: {gender}</h2>
         <h2>Origin: {origin.name}</h2>
         <h2 className="keyId">Key: {id}</h2>
         <br></br>
         <img class="alineadoAbajo" src={image} alt={'Character'} />
      </div>
   );
}
